import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PopoverController } from '@ionic/angular';
import { AddSubjectComponent } from '../Components/add-subject/add-subject.component';
import { ModalController } from '@ionic/angular';
import { IndexService } from '../Shared/index.service';

import io from 'socket.io-client';
import { environment } from './../../environments/environment';

@Component({
  selector: 'app-folder',
  templateUrl: './folder.page.html',
  styleUrls: ['./folder.page.scss'],
})
export class FolderPage implements OnInit {
  public folder: string;
  obj: any;
  maths: any;
  call: any;
  fastquestions: boolean = true;
  zim: boolean = false;
  courses: any;

  slideOpts = {
    initialSlide: 0,
    slidesPerView: 2,
    speed: 400,
  };
  socket: any;

  constructor(
    private activatedRoute: ActivatedRoute,
    public popoverController: PopoverController,
    public modalctrl: ModalController,
    private indexser: IndexService
  ) {
    this.socket = io(environment.apibaseurl);
  }

  ionViewWillEnter() {
    this.call = 'zoom';
    this.folder = this.activatedRoute.snapshot.paramMap.get('id');

    if (this.folder === 'FastQuestions') {
      this.fastquestions = true;
      console.log(this.fastquestions);
    } else {
      this.zim = true;
      this.fastquestions = false;
      console.log(this.zim);
    }
  }

  ngOnInit() {
    this.getCourses();
    this.socket.on('refreshgettingCourse', (data: any) => {
      this.getCourses();
    });
  }

  getCourses() {
    this.indexser.getCourse().subscribe(
      (res: any) => {
        console.log(res);
        this.courses = res.record;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  removeFromarray(id: any) {
    this.maths = this.maths.filter((el) => {
      return el.id != id;
    });
  }

  async presentModal() {
    const modal = await this.modalctrl.create({
      component: AddSubjectComponent,
      cssClass: 'my-custom-class',
    });
    return await modal.present();
  }
}
